<script>
  import { ref } from 'vue';  // 從 Vue 中導入 ref
  import { Swiper, SwiperSlide } from 'swiper/vue';
  import 'swiper/css';
  import 'swiper/css/navigation';
  import 'swiper/css/pagination';
  import 'swiper/css/thumbs';
  import 'swiper/css/free-mode';
  import { Navigation, Pagination, Mousewheel, Keyboard, Autoplay, FreeMode, Thumbs } from 'swiper/modules';
  
  export default {
    name: 'PageTumbs',
    components: {
      Swiper,
      SwiperSlide
    },
      setup() {
      const thumbsSwiper = ref(null);  // 使用 ref 來創建一個響應式的 swiper 實例引用

      return {
        thumbsSwiper,  // 返回 thumbsSwiper 給模板使用
        modules: [Navigation, Pagination, Mousewheel, Keyboard, Autoplay, Thumbs, FreeMode]
    };
  }
};
