<template>
  <div class="image-container">
    <!-- 主圖片 Swiper，使用縮略圖進行聯動 -->
    <swiper
      :navigation="true"
      :pagination="{
        clickable: true
      }"
      :mousewheel="{
        invert: false,
        forceToAxis: false,
        releaseOnEdges: true
      }"
      :keyboard="true"
      :loop="true"
      :autoplay="{
        delay: 5000,
        disableOnInteraction: false,
        pauseOnMouseEnter: true
      }"
      :thumbs="{ swiper: thumbsSwiper }" 
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide>
        <img :src="image" :alt="altText" /> 
      </swiper-slide>

      <swiper-slide>
        <img :src="image" :alt="altText" /> 
      </swiper-slide>

      <swiper-slide>
         <img :src="image" :alt="altText" /> 
      </swiper-slide>
      
    </swiper>

    <!-- 縮略圖 Swiper -->
    <swiper
      @swiper="setThumbsSwiper"
      :spaceBetween="10"
      :slidesPerView="5"
      :freeMode="true"
      :watchSlidesProgress="true"
      :modules="modules"
      :breakpoints="{
      '640': {
        slidesPerView: 2,
        spaceBetween: 10,
      },
      '768': {
        slidesPerView: 4,
        spaceBetween: 10,
      },
      '1024': {
        slidesPerView: 5,
        spaceBetween: 10,
      },
    }"
      class="mySwiper-thumbs"
    >
      <swiper-slide>
        <img src="@/assets/3CoffeeCocoaStrawberry(咖啡可可草莓)/518081.jpg" alt="咖啡可可草莓" />
      </swiper-slide>
      <swiper-slide>
        <img src="@/assets/4MatchaCitrusPastry(抹茶蜜柚蘭姆栗子)/494007.jpg" alt="抹茶蜜柚蘭姆栗子" />
      </swiper-slide>
      <swiper-slide>
        <img src="@/assets/6ChestnutPersimmonTea(栗香柿子紅茶)/482132.jpg" alt="栗香柿子紅茶" />
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { ref } from 'vue';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/thumbs';
import 'swiper/css/free-mode';
import { Navigation, Pagination, Mousewheel, Keyboard, Autoplay, FreeMode, Thumbs } from 'swiper/modules';


export default {
  name: 'PageImg',
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    const thumbsSwiper = ref(null);
    

    const setThumbsSwiper = (swiper) => {
      if (swiper) {
        thumbsSwiper.value = swiper;
      }
    };

    return {
      thumbsSwiper,
      setThumbsSwiper,
      modules: [Navigation, Pagination, Mousewheel, Keyboard, Autoplay, Thumbs, FreeMode]
    };
  },
  props:['image','altText']
};
</script>


<style scoped>


/* 設置圖片輪播容器的寬高，限制圖片大小 */
.image-container {
  width: 500px;
  height: auto;
  aspect-ratio: 1/1;/* 寬高比為 1:1，保持方形 */
  overflow: hidden; /* 隱藏超出部分 */
}

.mySwiper {
  width: 100%;
  height: 80%; /* 主 Swiper 占比 80% */
}

.mySwiper-thumbs {
  width: 100%;
  height: 20%; /* 縮略圖 Swiper 占比 20% */
  margin-top: 10px; 
}

.mySwiper-thumbs .swiper-slide {
  margin-right: 0; /* 移除縮圖之間的 margin */
  width: auto; /* 讓縮圖自動調整寬度 */
}

.swiper-slide img {
  width: 100%; /* 圖片寬度設為 100%，適應容器 */
  height: 100%; /* 圖片高度設為 100%，保持一致大小 */
  object-fit: cover; /* 保持圖片比例，不會拉伸 */
  cursor: pointer;
}

.swiper-pagination-bullet {
  width: 30px;
  height: 10px;
  border-radius: 15px;
  opacity: 0.6;
  margin: 0.5px;
}

.swiper-pagination-bullet-active {
  opacity: 1;
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  top: 50%; /* 將按鈕垂直居中 */
  transform: translateY(-50%); /* 垂直居中的變換 */
  z-index: 10; /* 保證按鈕在圖片之上 */
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.5); /* 背景色為半透明黑色 */
  border-radius: 50%; /* 將按鈕變成圓形 */
}

/* 調整按鈕的間距，不超出圖片容器 */
.swiper-button-next {
  right: 10px; /* 距離右邊 10px */
}

.swiper-button-prev {
  left: 10px; /* 距離左邊 10px */
}

@media (max-width: 768px) {
  .image-container {
    width: 200%;
    height: auto; /* 在小螢幕上高度設置為自動 */
    aspect-ratio: 1/1;
  }

  .mySwiper {
    height: 60%; /* 小螢幕上主 Swiper 占比調小 */
  }

  .mySwiper-thumbs {
    display: none; /* 小螢幕上縮略圖 Swiper 占比調小 */
  }

  .swiper-slide img {
    object-fit: contain; /* 在小螢幕上確保圖片不被裁剪 */
  }
  
  .swiper-button-next,
  .swiper-button-prev {
    width: 20px; /* 縮小按鈕的大小 */
    height: 20px;
  }

  .swiper-button-next {
    right: 5px; /* 縮小按鈕與圖片的距離 */
  }

  .swiper-button-prev {
    left: 5px; /* 縮小按鈕與圖片的距離 */
  }
}
</style>
